.message {
  position         : absolute;
  right            : 20px;
  bottom           : 10px;
  color            : white;
  font-family      : "Josefin Slab", serif;
  line-height      : 27px;
  font-size        : 18px;
  text-align       : right;
  pointer-events   : none;
  -webkit-animation: message-frames 1.5s ease 5s forwards;
  animation        : message-frames 1.5s ease 5s forwards;
  opacity          : 0;
}

@-webkit-keyframes message-frames {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes message-frames {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}